body {
  //max-width: 600px;
  //margin: 40px auto;
  background-color: #e7e6e0;
  padding: 0 10px;
  //font: 18px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  color: #444;
}

.essay {
  max-width: 700px;
  margin: 0em auto;
  padding: 0 10px;
  //font: 18px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  color: #444;
}

p {
  //font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-family: "Vollkorn", serif;
  font-size: 1.5em;
}

li,
ul {
  font-family: "Vollkorn", serif;
  font-size: 1.25em;
}

.post_link {
  font-family: "Vollkorn", serif;
  font-size: 1.5em;
  margin-top: -0.2em;
  margin-bottom: -0.6em;
}

h1,
h2,
h3 {
  line-height: 1.2;
}

h1 {
  font-family: "Vollkorn", serif;
  font-optical-sizing: auto;
  font-weight: 900;
  font-style: normal;
  font-size: 3em;
  margin: 0em;
}

.poem_title {
  font-family: "Vollkorn", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: oblique;
  font-size: 3em;
  margin: 0em;
}

h2 {
  font-family: "Vollkorn", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 2.5em;
  margin: 0em;
}

h3 {
  font-family: "Vollkorn", serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  font-size: 1.75em;
  margin: 0em;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:link {
  color: #2e5538;
}

a:visited {
  color: #2e5538;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #c9d1d9;
    background: #0d1117;
  }

  a:link {
    color: #58a6ff;
  }

  a:visited {
    color: #8e96f0;
  }
}

img {
  max-width: 600px;
}

.vollkorn-sc-regular {
  font-family: "Vollkorn SC", serif;
  font-weight: 400;
  font-style: normal;
}

.vollkorn-sc-semibold {
  font-family: "Vollkorn SC", serif;
  font-weight: 600;
  font-style: normal;
}

.vollkorn-sc-bold {
  font-family: "Vollkorn SC", serif;
  font-weight: 700;
  font-style: normal;
}

.vollkorn-sc-black {
  font-family: "Vollkorn SC", serif;
  font-weight: 900;
  font-style: normal;
}

// <uniquifier>: Use a unique and descriptive class name
// <weight>: Use a value from 400 to 900
.vollkorn-k {
  font-family: "Vollkorn", serif;
  font-optical-sizing: auto;
  font-weight: 900;
  font-style: normal;
}